import React, { Component } from "react";
import * as d3 from "d3";

import "./IncomeInequality.css";

const HEIGHT = 400;

export default class IncomeInequality extends Component {
    constructor(props) {
        super(props);
        this.plot = this.plot.bind(this);
        this.state = { complete: false };
    }

    setup() {
        const margin = { top: 20, right: 20, bottom: 30, left: 40 };
        const svg = d3.select(this.svg);
        this.width = svg.node().parentNode.getBoundingClientRect().width;
        this.height = HEIGHT - margin.top - margin.bottom;
        this.colors = ["grey", "maroon"];

        svg
            .attr("width", this.width + margin.left + margin.right)
            .attr("height", this.height + margin.top + margin.bottom)
            .append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        let legend = svg
            .select("g.legend")
            .selectAll("g")
            .data([0, 1])
            .enter()
            .append("g")
            .attr("class", "legend")
            .attr("transform", (d, i) => "translate(0," + i * 20 + ")");

        legend
            .append("rect")
            .attr("x", 0)
            .attr("y", 12)
            .attr("width", 18)
            .attr("height", 18)
            .style("fill", (d, i) => this.colors[i]);

        legend
            .append("text")
            .attr("x", 22)
            .attr("y", 20)
            .attr("dy", ".35em")
            .text((d, i) => ["Top 10% of Population", "Bottom 90% of Population"][i]);
    }

    hideTooltip() {
        d3.select("#tooltip").style("opacity", 0);
    }

    componentDidUnmount() {
        window.removeEventListener("click", this.hideTooltip);
    }

    componentDidMount() {
        window.addEventListener("click", this.hideTooltip);

        this.setup();
        d3
            .csv("data/dfa-networth-levels.csv")
            .then(rows => {
                let raw = rows
                    .filter(row => row.Date.endsWith(":Q1"))
                    .map(row => {
                        row.year = row.Date.split(":")[0];
                        return row;
                    })
                    .reduce(
                        (reducer, row) => {
                            if (!reducer.year.includes(row.year)) {
                                reducer.year.push(row.year);
                            }
                            let index = reducer.year.indexOf(row.year);
                            if (["Top1", "Next9"].includes(row.Category)) {
                                let start = reducer.top10[index] || 0;
                                reducer.top10[index] = parseInt(row["Net worth"]) + start;
                            } else if (["Next40", "Bottom50"].includes(row.Category)) {
                                let start = reducer.bottom90[index] || 0;
                                reducer.bottom90[index] = parseInt(row["Net worth"]) + start;
                            }
                            return reducer;
                        },
                        { year: [], top10: [], bottom90: [] }
                    );

                let rates = {
                    year: raw.year,
                    top10: raw.top10.map((value, i) => value / (value + raw.bottom90[i])),
                    bottom90: raw.bottom90.map((value, i) => value / (value + raw.top10[i]))
                };
                return rates;
            })
            .then(this.plot);
    }

    /**
     * Recursively plots the data for an animated plot until it is done
     * @param {Object} preparedData
     * @param {int} bars - the number of columns to plot
     */
    plot(preparedData, bars = 1) {
        const n = bars; // number of samples
        const m = 2; // number of series

        const svg = d3.select(this.svg);
        const columns = [preparedData.top10.slice(0, bars), preparedData.bottom90.slice(0, bars)];

        const y = d3.scaleLinear().domain([1, 0]).range([this.height, 0]);
        const x0 = d3.scaleBand().domain(d3.range(n)).range([0, this.width], 0.2);
        const x1 = d3.scaleBand().domain(d3.range(m)).range([0, x0.bandwidth() - 10]);
        const x2 = d3.scaleBand().domain(preparedData.year.slice(0, bars)).range([0, this.width], 0.2);
        const z = d3.scaleOrdinal().range(this.colors);

        svg.select("g.bars").selectAll("g").remove();

        svg
            .select("g.bars")
            .selectAll("g")
            .data(columns)
            .enter()
            .append("g")
            .style("fill", (d, i) => z(i))
            .attr("transform", (d, i) => "translate(" + x1(i) + ",0)")
            .selectAll("rect")
            .data(d => d)
            .enter()
            .append("rect")
            .attr("id", (d, i) => preparedData.year[i])
            .attr("width", x1.bandwidth())
            .attr("height", y)
            .attr("x", (d, i) => x0(i))
            .attr("y", d => this.height - y(d))
            .on("mouseover", (d, i) => {
                let tip = d3.select("#tooltip");
                tip.transition().duration(50).style("opacity", 0.9);
                tip
                    .html(`${preparedData.year[i]}<br/>${d3.format(".2f")(d * 100)}% of US Wealth`)
                    .style("left", d3.event.pageX + 10 + "px")
                    .style("top", d3.event.pageY - 75 + "px");
            })
            .on("mouseout", this.hideTooltip);

        svg
            .select("g.x-axis")
            .attr("transform", `translate(0,${HEIGHT - 50})`)
            .call(d3.axisBottom().scale(x2).tickValues(preparedData.year.slice(0, bars)));

        // Countdown
        const ratio = preparedData.top10[bars - 1] / preparedData.bottom90[bars - 1];
        d3
            .select("g.tracking-info")
            .attr("transform", `translate(${this.width},30)`)
            .select("#counter")
            .text(`${preparedData.year[bars - 1]} Ratio: ${d3.format("(.0f")(ratio * 100)}%`);

        if (bars < preparedData.year.length) {
            setTimeout(() => this.plot(preparedData, bars + 1), 250);
        } else {
            this.setState({ complete: true });
        }
    }

    render() {
        return (
            <div>
                <h1>Income Inequality in the United States</h1>
                <svg width="100%" ref={svg => (this.svg = svg)}>
                    <g className="bars" />
                    <g className="x-axis" />
                    <g className="legend" />
                    <g className="tracking-info">
                        <text id="counter" style={{ textAnchor: "end" }} className="strong h4" />
                        <text id="static" y="20" style={{ textAnchor: "end" }} className="text-muted small">
                            (Top 10% Wealth / Bottom 90% Wealth)
                        </text>
                    </g>
                </svg>
                <div id="tooltip" className="alert alert-info" style={{ opacity: 0, position: "absolute" }} />
                {this.state.complete &&
                    <div className="row form-group animate__animated animate__fadeInUp">
                        <div className="offset-md-3 col-md-3">
                            <button className="btn btn-info btn-block" onClick={() => this.props.onNavigate("ceopay")}>
                                <i className="fa fa-user-tie" /> Dive into CEO Pay
                            </button>
                        </div>
                        <div className="col-md-3">
                            <button
                                className="btn btn-info btn-block"
                                onClick={() => this.props.onNavigate("teacherpay")}
                            >
                                <i className="fa fa-chalkboard-teacher" /> Dive into Teacher Pay
                            </button>
                        </div>
                    </div>}
                <div className="alert alert-info">
                    <p>
                        The chart above shows the percentage of weath in the United States, divided between two groups
                        of people: the top 10% and the rest (bottom 90%). You can see that over time, the top 10% of the
                        population has been accumulating a larger share of the weath.
                    </p>
                    <small>
                        <em>
                            Source:{" "}
                            <a
                                href="https://www.federalreserve.gov/releases/z1/dataviz/dfa/distribute/chart/#range:1989.3,2020.1;quarter:122;series:Net%20worth;demographic:networth;population:1,3,5,7;units:levels"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                Federal Reserve
                            </a>
                        </em>
                    </small>
                </div>
            </div>
        );
    }
}
