import React, { Component } from "react";

import CeoPay from "./viz/CeoPay";
import IncomeInequality from "./viz/IncomeInequality";
import TeacherPay from "./viz/TeacherPay";

export default class App extends Component {
    constructor(props) {
        super(props);
        this.navigate = this.navigate.bind(this);
        this.state = { chart: "intro" };
    }

    navigate(chart) {
        this.setState({ chart });
    }

    render() {
        return (
            <div>
                <div className="bg-dark text-white">
                    <div className="container">
                        UIUC Summer 2020 • CS498 Data Visualization • Final Project • Ryan Kruse (rjkruse2)
                    </div>
                </div>
                {this.state.chart === "intro" &&
                    <div className="jumbotron">
                        <div className="container text-center">
                            <h1>Income Inequality in the United States</h1>
                            <br />
                            <p className="text-muted">
                                <i className="fa fa-fw fa-user-tie fa-3x" />
                                <i className="fa fa-fw fa-money fa-3x" style={{ margin: "0 1em" }} />
                                <i className="fa fa-fw fa-chalkboard-teacher fa-3x " />
                            </p>
                            <button
                                className="btn btn-lg btn-info"
                                onClick={() => this.setState({ chart: "inequality" })}
                            >
                                Get Started
                            </button>
                        </div>
                    </div>}
                <div className="container">
                    {this.state.chart === "inequality" && <IncomeInequality onNavigate={this.navigate} />}
                    {this.state.chart === "ceopay" && <CeoPay onNavigate={this.navigate} />}
                    {this.state.chart === "teacherpay" && <TeacherPay onNavigate={this.navigate} />}
                </div>
            </div>
        );
    }
}
