import React, { Component } from "react";
import * as d3 from "d3";

import "./CeoPay.css";

const HEIGHT = 400;

export default class CeoPay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
    }

    componentDidMount() {
        const margin = { top: 20, right: 20, bottom: 30, left: 40 };
        const svg = d3.select(this.svg);
        const width = svg.node().parentNode.getBoundingClientRect().width - margin.left - margin.right;
        const height = HEIGHT - margin.top - margin.bottom;

        svg.attr("width", width + margin.left + margin.right).attr("height", height + margin.top + margin.bottom);

        const parseTime = d3.timeParse("%Y");
        const bisectDate = d3.bisector(d => d.year).left;

        const x = d3.scaleTime().range([0, width]);
        const y = d3.scaleLinear().range([height, 0]);

        const line = d3.line().x(d => x(d.year)).y(d => y(d.payRatio));
        const g = svg.append("g").attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        d3.csv("data/ceopay.csv").then(rows => {
            let data = rows.map(row => {
                return { year: parseTime(row.year), payRatio: +row.optionsRealized };
            });
            this.setState({ data });

            x.domain(d3.extent(data, d => d.year));
            y.domain([d3.min(data, d => d.payRatio) / 1.005, d3.max(data, d => d.payRatio) * 1.005]);

            g
                .append("g")
                .attr("class", "axis axis--x")
                .attr("transform", "translate(0," + height + ")")
                .call(d3.axisBottom(x));

            g
                .append("g")
                .attr("class", "axis axis--y")
                .call(
                    d3.axisLeft(y).ticks(6).tickFormat(d => {
                        return parseInt(d) + "x";
                    })
                )
                .append("text")
                .attr("class", "axis-title")
                .attr("transform", "rotate(-90)")
                .attr("y", 6)
                .attr("dy", ".71em")
                .style("text-anchor", "end")
                .attr("fill", "#5D6971")
                .text("CEO Pay/Worker Ratio)");

            g
                .append("g")
                .attr("class", "hints")
                .selectAll("line")
                .data([new Date(1980, 1, 1), new Date(2018, 1, 1)])
                .enter()
                .append("line")
                .attr("class", "hint")
                .attr("x1", d => x(d))
                .attr("x2", d => x(d))
                .attr("y1", 0)
                .attr("y2", height);

            g.append("path").datum(data).attr("class", "line").attr("d", line);

            let focus = g.append("g").attr("class", "focus").style("display", "none");
            focus.append("line").attr("class", "x-hover-line hover-line").attr("y1", 0).attr("y2", height);
            focus.append("line").attr("class", "y-hover-line hover-line").attr("x1", width).attr("x2", width);
            focus.append("circle").attr("r", 7.5);
            focus.append("text").attr("x", 15).attr("dy", ".31em");

            svg
                .append("rect")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")")
                .attr("class", "overlay")
                .attr("width", width)
                .attr("height", height)
                .on("mouseover", () => focus.style("display", null))
                .on("mouseout", () => focus.style("display", "none"))
                .on("mousemove", mousemove);

            function mousemove() {
                let x0 = x.invert(d3.mouse(this)[0]),
                    i = bisectDate(data, x0, 1),
                    d0 = data[i - 1],
                    d1 = data[i],
                    d = x0 - d0.year > d1.year - x0 ? d1 : d0;
                focus.attr("transform", "translate(" + x(d.year) + "," + y(d.payRatio) + ")");
                focus.select("text").text(`${d.year.getFullYear()}: ${d.payRatio}x`);
                focus.select(".x-hover-line").attr("y2", height - y(d.payRatio));
                focus.select(".y-hover-line").attr("x2", width + width);
            }
        });
    }

    render() {
        let first = this.state.data.find(d => d.year.getFullYear() === 1980);
        let last = this.state.data.find(d => d.year.getFullYear() === 2018);

        return (
            <div id="ceopay">
                <h1>
                    CEO Pay vs Workforce <small className="text-muted">(Top 350 Firms)</small>
                </h1>
                <svg width="100%" ref={svg => (this.svg = svg)} />
                {first &&
                    <div id="first-tip" className="alert alert-warning">
                        In <strong>{first.year.getFullYear()}</strong>, US CEOs took home{" "}
                        <strong>{first.payRatio}</strong> times their average worker.
                    </div>}
                {last &&
                    <div id="last-tip" className="alert alert-danger">
                        In <strong>{last.year.getFullYear()}</strong>, the ratio rose to{" "}
                        <strong>{last.payRatio}</strong> times worker pay.
                    </div>}
                <div className="row form-group animate__animated animate__fadeInUp">
                    <div className="offset-md-3 col-md-3">
                        <button className="btn btn-info btn-block" onClick={() => this.props.onNavigate("inequality")}>
                            <i className="fa fa-money" /> Back to Income Inequality
                        </button>
                    </div>
                    <div className="col-md-3">
                        <button className="btn btn-info btn-block" onClick={() => this.props.onNavigate("teacherpay")}>
                            <i className="fa fa-chalkboard-teacher" /> Dive into Teacher Pay
                        </button>
                    </div>
                </div>
                <div className="alert alert-info">
                    <p>
                        One of the things we can look to explain how the top 10% keep getting richer, is the rate at
                        which US corporations pay their CEOs compared to the rest of the workforce. The chart above,
                        shows how CEO pay within the top 350 firms in the US started creeping steadily from the mid 70's
                        through the 90's, before starting to take off like a rocket ship.
                    </p>
                    <small>
                        <em>
                            Source:{" "}
                            <a
                                href="https://www.epi.org/publication/ceo-compensation-2018/"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                Economic Policy Institute
                            </a>
                        </em>
                    </small>
                </div>
            </div>
        );
    }
}
