import React, { Component } from "react";
import * as d3 from "d3";

import "./TeacherPay.css";

const HEIGHT = 400;

export default class CeoPay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
    }

    componentDidMount() {
        const margin = { top: 20, right: 20, bottom: 30, left: 40 };
        const svg = d3.select(this.svg);
        const width = svg.node().parentNode.getBoundingClientRect().width - margin.left - margin.right;
        const height = HEIGHT - margin.top - margin.bottom;

        svg.attr("width", width + margin.left + margin.right).attr("height", height + margin.top + margin.bottom);

        const parseTime = d3.timeParse("%Y");
        const bisectDate = d3.bisector(d => d.year).left;

        const x = d3.scaleTime().range([0, width]);
        const y = d3.scaleLinear().range([height, 0]);

        const line = d3.line().x(d => x(d.year)).y(d => y(d.salary));
        const g = svg.append("g").attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        d3.csv("data/teacherpay.csv").then(rows => {
            let data = rows.map(row => {
                return {
                    year: parseTime(row.year),
                    salary: +row.salary,
                    currentDayDollars: +row.e2019
                };
            });

            let cpi = [
                { year: data[0].year, salary: data[0].salary },
                { year: data[data.length - 1].year, salary: data[0].currentDayDollars }
            ];

            this.setState({ data });

            x.domain(d3.extent(data, d => d.year));
            y.domain([d3.min(data, d => d.salary) / 1.005, d3.max(data, d => d.salary) * 1.005]);

            g
                .append("g")
                .attr("class", "axis axis--x")
                .attr("transform", "translate(0," + height + ")")
                .call(d3.axisBottom(x));

            g
                .append("g")
                .attr("class", "axis axis--y")
                .call(
                    d3.axisLeft(y).ticks(6).tickFormat(d => {
                        return parseInt(d) / 1000 + "k";
                    })
                )
                .append("text")
                .attr("class", "axis-title")
                .attr("transform", "rotate(-90)")
                .attr("y", 6)
                .attr("dy", ".71em")
                .style("text-anchor", "end")
                .attr("fill", "#5D6971")
                .text("Dollars");

            g.append("path").datum(data).attr("class", "line").attr("d", line);
            g.append("path").datum(cpi).attr("class", "cpi").attr("d", line);

            let focus = g.append("g").attr("class", "focus").style("display", "none");
            focus.append("line").attr("class", "x-hover-line hover-line").attr("y1", 0).attr("y2", height);
            focus.append("line").attr("class", "y-hover-line hover-line").attr("x1", width).attr("x2", width);
            focus.append("circle").attr("r", 7.5);
            focus.append("text").attr("x", 15).attr("dy", ".31em");

            svg
                .append("rect")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")")
                .attr("class", "overlay")
                .attr("width", width)
                .attr("height", height)
                .on("mouseover", () => focus.style("display", null))
                .on("mouseout", () => focus.style("display", "none"))
                .on("mousemove", mousemove);

            function mousemove() {
                let x0 = x.invert(d3.mouse(this)[0]),
                    i = bisectDate(data, x0, 1),
                    d0 = data[i - 1],
                    d1 = data[i],
                    d = x0 - d0.year > d1.year - x0 ? d1 : d0;
                focus.attr("transform", "translate(" + x(d.year) + "," + y(d.salary) + ")");
                focus.select("text").text(`${d.year.getFullYear()}: $${d.salary}`);
                focus.select(".x-hover-line").attr("y2", height - y(d.salary));
                focus.select(".y-hover-line").attr("x2", width + width);
            }
        });
    }

    render() {
        let first = this.state.data[0];
        let last = this.state.data[this.state.data.length - 1];

        return (
            <div id="teacherpay">
                <h1>Teacher Pay vs Inflation</h1>
                <svg width="100%" ref={svg => (this.svg = svg)} />
                {first &&
                    <div id="first-tip" className="alert alert-warning">
                        In <strong>{first.year.getFullYear()}</strong>, US teachers earned an average of{" "}
                        <strong>${first.salary}</strong>, or <strong>${first.currentDayDollars}</strong> in 2018
                        dollars.
                    </div>}
                {last &&
                    <div id="last-tip" className="alert alert-danger">
                        In <strong>{last.year.getFullYear()}</strong>, teachers actually earned{" "}
                        <strong>${last.salary}</strong> , or <strong>${first.currentDayDollars - last.salary}</strong>{" "}
                        less than what would have been expected from their 1990 salaries.
                    </div>}
                <div className="row form-group animate__animated animate__fadeInUp">
                    <div className="offset-md-3 col-md-3">
                        <button className="btn btn-info btn-block" onClick={() => this.props.onNavigate("inequality")}>
                            <i className="fa fa-money" /> Back to Income Inequality
                        </button>
                    </div>
                    <div className="col-md-3">
                        <button className="btn btn-info btn-block" onClick={() => this.props.onNavigate("ceopay")}>
                            <i className="fa fa-user-tie" /> Dive into CEO Pay
                        </button>
                    </div>
                </div>
                <div className="alert alert-info">
                    <p>
                        In a previous slide, we saw that CEOs rapidly increased their earnings throughout the last few
                        decades. Here, we can see that teacher salaries have been more linear. In fact, if we look at
                        the rate of inflation (via the Consumer Price Index), we can see that teachers have actually
                        taken a pay reduction over the last three decades.
                    </p>
                    <small>
                        <em>
                            Source:{" "}
                            <a
                                href="https://nces.ed.gov/programs/digest/d19/tables/dt19_211.50.asp"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                National Center for Education Statistics
                            </a>
                        </em>
                    </small>
                </div>
            </div>
        );
    }
}
